@import "reset";
@import "loader";
@import "variables";

html {
  font-size: 62.5%;
  font-family: $weakFont;
}

html,
body {
  height: auto;
  min-width: 100%;
  height: 100%;
  background-color: transparent;
}

a {
  text-decoration: none;
  color: $main-color;

  &:hover {
    opacity: 0.7;
  }
}

/* Styles relative to the homepage layer*/

#layer-1 {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center;
  // top: 50%;
  // left: 50%;
  // transform: translate(-50%, -50%);
  // margin-left: auto;
  // margin-right: auto;
  font-size: 100%;
  height: 100%;
  width: 80%;
  background-color: #fff;

  img {
    width: 12%;
    min-width: 12rem;
    margin-top: 9.5%;
  }

  h1 {
    margin: 3% auto;
    width: 90%;
    text-align: center;
    font-size: 3.6rem;
    font-family: $strongFont;
    font-weight: bold;
  }

  p {
    width: 65%;
    font-size: 1.6rem;
  }

  button {
    font-size: 1.5rem;
    font-family: $strongFont;
    width: 100px;
    height: 40px;
    margin: 4% auto;
    padding: 0;
    border: none;
    background: none;
    outline: none;
    cursor: pointer;
    text-transform: uppercase;
    background: $main-color;
    border: 2px solid #56a0b3;
    border-radius: 0.75em;
    transform-style: preserve-3d;
    transition: transform 150ms cubic-bezier(0, 0, 0.58, 1),
      background 150ms cubic-bezier(0, 0, 0.58, 1);

    &::before {
      position: absolute;
      content: "";
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: #5a8a96;
      border-radius: inherit;
      box-shadow: 0 0 0 2px #56a0b3, 0em 0.625em 0 #cbdfe4;
      transform: translate3d(0, 0.5em, -1em);
      transition: transform 150ms cubic-bezier(0, 0, 0.58, 1),
        box-shadow 150ms cubic-bezier(0, 0, 0.58, 1);
    }

    &:active {
      transform: translate(0em, 0.5em);
    }

    &:hover {
      background: #71d6f1;
    }
  }

  ul {
    margin-bottom: 35px;
    font-size: 2.2rem;
    width: 100%;

    li {
      display: inline-block;
      margin: 0 1rem;
    }
  }

  footer {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 0);

    margin-bottom: 0.8rem;
    font-size: 1.3rem;
  }
}

/* Styles relative to the projects layer*/

#layer-3 {
  position: absolute;
  min-width: 100%;
  min-height: 100%;
  background-color: transparent;
  z-index: -98;
  height: 0%;
}

.wrap-element {
  text-align: center;
  margin: 0 auto;
  display: block;
}

#projects-container {
  position: relative;
  margin: 0 auto;
  width: 92%;
  //min-height: 80%;
  height: auto;
  background-color: transparent;

  ul {
    li {
      display: block;
      margin: 150px 0 200px 0;
    }
  }
}

.project-image {
  display: inline-block;
  width: 45%;
  padding-top: 20px;
}

.project-description {
  margin-top: 0;
  width: 43%;
  display: inline-block;
  float: right;

  h2 {
    margin-bottom: 30px;
    font-family: $strongFont;
    color: #fff;
    font-size: 2.5rem;
  }

  p {
    color: #737375;
    font-size: 2rem;
    line-height: 3.3rem;
  }
}

#goback {
  position: sticky;
  top: 13%;
  left: 4%;
  color: #fff;
  font-family: $strongFont;
  font-size: 1.85rem;
  font-weight: 800;
  border: none;
  background: none;
  outline: none;
  cursor: pointer;
  z-index: 2;

  &:hover {
    background-color: #fff;
    color: #000;
  }
}

/*Styles relative to the 3D main layer*/
#layer-2 {
  position: absolute;
  min-width: 100%;
  min-height: 100%;
  background-color: transparent;
  z-index: -99;
}

canvas {
  position: fixed;
  display: block;
  z-index: -9999;
}

@media only screen and (max-width: 1000px) {
  .project-description {
    h2 {
      font-size: 2rem;
    }

    p {
      font-size: 1.4rem;
      line-height: 2.3rem;
    }
  }
}

@media only screen and (max-width: 600px) {
  html {
    font-size: 60%;
  }

  .project-description {
    h2 {
      font-size: 1.5rem;
    }

    p {
      font-size: 1.3rem;
      line-height: 1.75rem;
    }
  }
}

@media only screen and (max-width: 450px) {
  html {
    font-size: 58%;
  }
  #layer-1 {
    h1 {
      margin: 30px auto !important;
    }

    p {
      width: 85%;
    }

    button {
      margin-top: 40px;
      margin-bottom: 30px;
    }
  }

  .project-description {
    h2 {
      font-size: 1.3rem;
    }

    p {
      font-size: 1rem;
      line-height: 1.5rem;
    }
  }
}

@media only screen and (max-width: 383px) {
  html {
    font-size: 47%;
  }
  footer {
    width: 100%;
  }
}

@media only screen and (max-width: 260px) {
  html {
    font-size: 43%;
  }
  footer {
    width: 100%;
  }
}
